.page-container {
  min-height: 100vh;
  background: 
    linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 25%, #16213e 50%, #0f3460 75%, #533483 100%),
    radial-gradient(circle at 20% 20%, rgba(255, 215, 0, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(255, 107, 157, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(196, 76, 240, 0.1) 0%, transparent 50%);
  padding: 25px;
  padding-bottom: 100px;
  position: relative;
  overflow: hidden;
}

.page-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 10% 10%, rgba(255, 215, 0, 0.05) 0%, transparent 40%),
    radial-gradient(circle at 90% 30%, rgba(255, 107, 157, 0.05) 0%, transparent 40%),
    radial-gradient(circle at 30% 90%, rgba(196, 76, 240, 0.05) 0%, transparent 40%);
  animation: background-drift 10s ease-in-out infinite;
}

@keyframes background-drift {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  33% { transform: translate(10px, -10px) rotate(1deg); }
  66% { transform: translate(-10px, 10px) rotate(-1deg); }
}

.page-header {
  text-align: center;
  margin-bottom: 40px;
  color: white;
  position: relative;
  z-index: 1;
}

.page-title {
  color: transparent !important;
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0, #00d4ff) !important;
  background-clip: text !important;
  -webkit-background-clip: text !important;
  margin-bottom: 15px !important;
  font-size: 32px !important;
  font-weight: 700 !important;
  text-shadow: 0 0 30px rgba(255, 215, 0, 0.5);
  animation: title-glow 3s ease-in-out infinite;
}

@keyframes title-glow {
  0%, 100% { 
    filter: brightness(1) drop-shadow(0 0 20px rgba(255, 215, 0, 0.6));
  }
  50% { 
    filter: brightness(1.2) drop-shadow(0 0 30px rgba(255, 107, 157, 0.8));
  }
}

.page-subtitle {
  color: rgba(255, 255, 255, 0.8) !important;
  font-size: 18px;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}

/* Swap页面专用样式 */
.swap-card {
  background: linear-gradient(135deg, 
    rgba(12, 12, 12, 0.9) 0%, 
    rgba(26, 26, 46, 0.85) 50%, 
    rgba(22, 33, 62, 0.9) 100%);
  border-radius: 25px;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 215, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 215, 0, 0.3);
  position: relative;
  overflow: hidden;
  padding: 30px;
  margin-bottom: 30px;
  transition: all 0.3s ease;
}

.swap-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(255, 215, 0, 0.05) 0%, 
    rgba(255, 107, 157, 0.05) 50%, 
    rgba(196, 76, 240, 0.05) 100%);
  animation: card-glow 4s ease-in-out infinite;
  pointer-events: none;
}

@keyframes card-glow {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.7; }
}

.swap-card:hover {
  transform: translateY(-5px);
  box-shadow: 
    0 35px 70px rgba(0, 0, 0, 0.6),
    0 0 0 1px rgba(255, 215, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.swap-card h3 {
  color: #ffd700;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  text-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
  position: relative;
  z-index: 1;
}

.swap-header {
  text-align: center;
  margin-bottom: 32px;
  z-index: 2;
  position: relative;
}

.swap-title {
  color: transparent;
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0);
  background-clip: text;
  -webkit-background-clip: text;
  font-size: 48px;
  font-weight: 800;
  margin: 0 0 16px 0;
  text-shadow: 0 0 30px rgba(255, 215, 0, 0.5);
  letter-spacing: -0.02em;
}

.swap-subtitle {
  color: #B8ADD2;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  opacity: 0.9;
}

.swap-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  position: relative;
  z-index: 1;
}

.swap-header h4 {
  color: #ffd700 !important;
  text-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

.settings-btn {
  color: #ffd700;
  background: rgba(255, 215, 0, 0.1);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.settings-btn:hover {
  color: #fff !important;
  background: rgba(255, 215, 0, 0.2) !important;
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.4);
}

.token-input-section {
  margin: 25px 0;
  position: relative;
  z-index: 1;
}

.input-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
}

.input-label {
  font-weight: 600;
  color: #ffd700;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.balance {
  color: rgba(255, 255, 255, 0.7);
  font-size: 13px;
}

.token-input {
  display: flex;
  gap: 15px;
  align-items: center;
}

.amount-input {
  flex: 1;
  border-radius: 15px !important;
  border: 2px solid rgba(255, 215, 0, 0.3) !important;
  background: rgba(0, 0, 0, 0.3) !important;
  color: white !important;
}

.amount-input:focus {
  border-color: #ffd700 !important;
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2), 0 0 20px rgba(255, 215, 0, 0.4) !important;
  background: rgba(0, 0, 0, 0.5) !important;
}

.amount-input::placeholder {
  color: rgba(255, 255, 255, 0.4) !important;
}

.token-select {
  min-width: 130px;
}

.token-select .ant-select-selector {
  border-radius: 15px !important;
  border: 2px solid rgba(255, 215, 0, 0.3) !important;
  background: rgba(0, 0, 0, 0.3) !important;
  color: white !important;
}

.token-select:hover .ant-select-selector {
  border-color: #ffd700 !important;
}

.swap-divider {
  text-align: center;
  margin: 30px 0;
  position: relative;
  z-index: 1;
}

.swap-btn {
  background: linear-gradient(135deg, #ff6b9d, #ffd700, #c44cf0) !important;
  border: none !important;
  width: 50px;
  height: 50px;
  box-shadow: 0 10px 25px rgba(255, 215, 0, 0.4);
  animation: swap-pulse 2s ease-in-out infinite;
}

@keyframes swap-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.swap-btn:hover {
  box-shadow: 0 15px 35px rgba(255, 215, 0, 0.6) !important;
}

.swap-info {
  margin: 25px 0;
  position: relative;
  z-index: 1;
}

.info-row {
  display: flex;
  justify-content: space-between;
  margin: 12px 0;
  color: rgba(255, 255, 255, 0.8);
  padding: 8px 0;
  border-bottom: 1px solid rgba(255, 215, 0, 0.1);
}

.swap-execute-btn {
  background: linear-gradient(135deg, #ff1744, #ffd700, #9c27b0, #00bcd4) !important;
  border: none !important;
  border-radius: 15px !important;
  height: 55px !important;
  font-size: 18px !important;
  font-weight: 700 !important;
  box-shadow: 0 15px 35px rgba(255, 23, 68, 0.4);
  position: relative;
  z-index: 1;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.swap-execute-btn:hover {
  box-shadow: 0 20px 45px rgba(255, 23, 68, 0.6) !important;
  transform: translateY(-2px);
}

/* IDO页面专用样式 */
.ido-card {
  background: linear-gradient(135deg, 
    rgba(12, 12, 12, 0.9) 0%, 
    rgba(26, 26, 46, 0.85) 50%, 
    rgba(22, 33, 62, 0.9) 100%);
  border-radius: 25px;
  margin-bottom: 25px;
  overflow: hidden;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 215, 0, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(255, 215, 0, 0.2);
  position: relative;
}

.ido-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(255, 215, 0, 0.03) 0%, 
    rgba(255, 107, 157, 0.03) 50%, 
    rgba(196, 76, 240, 0.03) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.ido-card:hover::before {
  opacity: 1;
}

.ido-card:hover {
  transform: translateY(-8px);
  box-shadow: 
    0 30px 60px rgba(0, 0, 0, 0.7),
    0 0 0 2px rgba(255, 215, 0, 0.4),
    0 0 30px rgba(255, 215, 0, 0.3);
}

.ido-banner {
  height: 140px;
  background: linear-gradient(135deg, #ff1744, #ffd700, #9c27b0, #00bcd4);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 20px;
  font-weight: 700;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
  position: relative;
  overflow: hidden;
}

.ido-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: banner-shine 3s infinite;
}

@keyframes banner-shine {
  0% { left: -100%; }
  100% { left: 100%; }
}

.ido-progress {
  margin: 20px 0;
}

.ido-stats {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.ido-stat {
  text-align: center;
  flex: 1;
}

.ido-stat-value {
  font-size: 20px;
  font-weight: 700;
  color: #ffd700;
  text-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

.ido-stat-label {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 6px;
}

/* 挖矿页面专用样式 */
.mining-dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
  margin-bottom: 25px;
}

.mining-card {
  background: linear-gradient(135deg, 
    rgba(12, 12, 12, 0.9) 0%, 
    rgba(26, 26, 46, 0.85) 50%, 
    rgba(22, 33, 62, 0.9) 100%);
  border-radius: 20px;
  padding: 25px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 215, 0, 0.2);
  border: 1px solid rgba(255, 215, 0, 0.2);
  transition: all 0.3s ease;
}

.mining-card:hover {
  transform: translateY(-5px);
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.7),
    0 0 0 2px rgba(255, 215, 0, 0.4);
}

.mining-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.mining-icon {
  font-size: 28px;
  margin-right: 15px;
  color: #ffd700;
  filter: drop-shadow(0 0 15px rgba(255, 215, 0, 0.5));
}

.mining-value {
  font-size: 28px;
  font-weight: 700;
  color: #ffd700;
  margin-bottom: 8px;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.5);
}

.mining-label {
  color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .page-container {
    padding: 20px;
  }
  
  .page-title {
    font-size: 28px !important;
  }
  
  .mining-dashboard {
    grid-template-columns: 1fr;
  }
  
  .swap-card,
  .ido-card,
  .mining-card {
    border-radius: 20px;
  }
}

/* 钱包连接样式 */
.connect-button {
  background: linear-gradient(135deg, #ffd700, #ff6b9d);
  color: #000;
  border: none;
  border-radius: 15px;
  padding: 15px 30px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.3);
  position: relative;
  z-index: 1;
  width: 100%;
}

.connect-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(255, 215, 0, 0.5);
  background: linear-gradient(135deg, #ff6b9d, #c44cf0);
}

.connect-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.wallet-info {
  position: relative;
  z-index: 1;
  color: white;
}

.wallet-info p {
  margin: 10px 0;
  font-size: 16px;
}

.wallet-info .address {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  word-break: break-all;
  background: rgba(0, 0, 0, 0.3);
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 215, 0, 0.2);
}

/* 输入组样式 */
.input-group {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;
}

.address-input {
  flex: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 15px;
  padding: 15px 20px;
  color: white;
  font-size: 16px;
  transition: all 0.3s ease;
}

.address-input:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2), 0 0 20px rgba(255, 215, 0, 0.4);
  background: rgba(0, 0, 0, 0.5);
}

.address-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.query-button {
  background: linear-gradient(135deg, #00d4ff, #c44cf0);
  color: white;
  border: none;
  border-radius: 15px;
  padding: 15px 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.3);
}

.query-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(0, 212, 255, 0.5);
}

.query-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* 代币信息样式 */
.token-info {
  position: relative;
  z-index: 1;
  margin-top: 20px;
}

.token-info h4 {
  color: #ffd700;
  font-size: 20px;
  margin-bottom: 15px;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  padding: 12px 16px;
  border-radius: 12px;
  border: 1px solid rgba(255, 215, 0, 0.2);
  transition: all 0.3s ease;
}

.info-item:hover {
  background: rgba(0, 0, 0, 0.5);
  border-color: rgba(255, 215, 0, 0.4);
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
}

.info-item span:first-child {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
}

.info-item span:last-child {
  color: white;
  font-weight: 700;
}

/* 交换界面样式 */
.swap-section {
  margin: 25px 0;
  position: relative;
  z-index: 1;
}

.swap-section label {
  display: block;
  color: #ffd700;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.swap-input-group {
  display: flex;
  gap: 15px;
  align-items: center;
}

.token-select {
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 15px;
  padding: 15px;
  color: white;
  font-size: 16px;
  min-width: 120px;
  transition: all 0.3s ease;
}

.token-select:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2);
}

.token-select option {
  background: #1a1a2e;
  color: white;
}

.amount-input {
  flex: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 15px;
  padding: 15px 20px;
  color: white;
  font-size: 18px;
  transition: all 0.3s ease;
}

.amount-input:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2), 0 0 20px rgba(255, 215, 0, 0.4);
  background: rgba(0, 0, 0, 0.5);
}

.amount-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.amount-input:read-only {
  background: rgba(0, 0, 0, 0.2);
  border-color: rgba(255, 255, 255, 0.2);
  cursor: not-allowed;
}

/* 交换图标 */
.swap-icon {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  position: relative;
  z-index: 1;
}

.swap-reverse {
  background: linear-gradient(135deg, #ffd700, #ff6b9d);
  color: #000;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.3);
  animation: swap-pulse 2s infinite;
}

.swap-reverse:hover {
  transform: translateY(-2px) rotate(180deg);
  box-shadow: 0 12px 35px rgba(255, 215, 0, 0.5);
  background: linear-gradient(135deg, #ff6b9d, #c44cf0);
}

/* 滑点设置 */
.slippage-section {
  margin: 25px 0;
  position: relative;
  z-index: 1;
}

.slippage-section label {
  display: block;
  color: #ffd700;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 15px;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.slippage-buttons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.slippage-btn {
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 12px;
  padding: 8px 16px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.slippage-btn:hover {
  border-color: #ffd700;
  background: rgba(255, 215, 0, 0.1);
}

.slippage-btn.active {
  background: linear-gradient(135deg, #ffd700, #ff6b9d);
  color: #000;
  border-color: #ffd700;
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.4);
}

.slippage-input {
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 12px;
  padding: 8px 12px;
  color: white;
  font-size: 14px;
  width: 80px;
  transition: all 0.3s ease;
}

.slippage-input:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.2);
}

/* 执行按钮 */
.swap-execute-button {
  background: linear-gradient(135deg, #00d4ff, #c44cf0, #ffd700);
  color: #000;
  border: none;
  border-radius: 20px;
  padding: 20px 40px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.4);
  width: 100%;
  margin-top: 25px;
  position: relative;
  z-index: 1;
  animation: button-glow 3s ease-in-out infinite;
}

@keyframes button-glow {
  0%, 100% {
    box-shadow: 0 10px 30px rgba(0, 212, 255, 0.4);
  }
  50% {
    box-shadow: 0 15px 40px rgba(255, 215, 0, 0.6);
  }
}

.swap-execute-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 45px rgba(0, 212, 255, 0.6);
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0);
}

.swap-execute-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  animation: none;
}

/* 消息样式 */
.message {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 16px 20px;
  border-radius: 16px;
  color: white;
  font-weight: 600;
  max-width: 420px;
  z-index: 1001;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  animation: slide-in 0.3s ease;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

@keyframes slide-in {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.message.error {
  background: linear-gradient(135deg, #ff4757, #ff3838);
  box-shadow: 0 15px 40px rgba(255, 71, 87, 0.5);
  border-color: rgba(255, 71, 87, 0.3);
}

.message.success {
  background: linear-gradient(135deg, #2ed573, #1dd1a1);
  box-shadow: 0 15px 40px rgba(46, 213, 115, 0.5);
  border-color: rgba(46, 213, 115, 0.3);
}

.message button {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 8px;
  transition: all 0.3s ease;
  flex-shrink: 0;
  min-width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.message button:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.1);
}

.message-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.message-content strong {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 2px;
}

.message-content p {
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
  opacity: 0.9;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .swap-card {
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .input-group {
    flex-direction: column;
  }
  
  .swap-input-group {
    flex-direction: column;
  }
  
  .slippage-buttons {
    justify-content: center;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
  }
  
  .message {
    right: 10px;
    left: 10px;
    max-width: none;
    top: 10px;
    padding: 12px 16px;
  }
  
  .message-content strong {
    font-size: 14px;
  }
  
  .message-content p {
    font-size: 13px;
  }
  
  .message button {
    min-width: 20px;
    height: 20px;
    font-size: 14px;
  }
}

/* PancakeSwap风格的Swap页面样式 */
.pancake-swap-container {
  min-height: 100vh;
  background: 
    linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 25%, #16213e 50%, #0f3460 75%, #533483 100%),
    radial-gradient(circle at 20% 20%, rgba(255, 215, 0, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(255, 107, 157, 0.1) 0%, transparent 50%);
  padding: 0 20px 100px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.pancake-swap-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 30% 20%, rgba(255, 215, 0, 0.08) 0%, transparent 40%),
    radial-gradient(circle at 70% 80%, rgba(196, 76, 240, 0.08) 0%, transparent 40%);
  animation: background-drift 8s ease-in-out infinite;
  pointer-events: none;
}

/* 顶部导航栏 */
.top-bar {
  width: 100%;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  margin-bottom: 20px;
  position: relative;
  z-index: 10;
}

.logo-section .app-logo {
  color: transparent;
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0);
  background-clip: text;
  -webkit-background-clip: text;
  font-size: 24px;
  font-weight: 800;
  margin: 0;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.5);
}

.wallet-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.connect-wallet-btn-top {
  background: linear-gradient(135deg, #1FC7D4, #7645D9);
  color: white;
  border: none;
  border-radius: 16px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(31, 199, 212, 0.3);
}

.connect-wallet-btn-top:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(31, 199, 212, 0.5);
  background: linear-gradient(135deg, #0098A1, #7645D9);
}

.connect-wallet-btn-top:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.wallet-connected {
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 16px;
  padding: 8px 16px;
  backdrop-filter: blur(10px);
}

.wallet-address {
  color: #F4EEFF;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
}

.wallet-status {
  color: #00D4AA;
  font-size: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 4px;
}

/* Header样式 */
.swap-header {
  text-align: center;
  margin-bottom: 40px;
  z-index: 1;
  position: relative;
}

.swap-title {
  font-size: 48px;
  font-weight: 800;
  color: transparent;
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0);
  background-clip: text;
  -webkit-background-clip: text;
  margin-bottom: 10px;
  text-shadow: 0 0 30px rgba(255, 215, 0, 0.5);
  animation: title-glow 3s ease-in-out infinite;
}

.swap-subtitle {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}



/* Swap卡片主体 */
.swap-card.pancake-style {
  background: linear-gradient(135deg, 
    rgba(39, 38, 44, 0.95) 0%, 
    rgba(26, 26, 46, 0.9) 50%, 
    rgba(22, 33, 62, 0.95) 100%);
  border-radius: 32px;
  padding: 24px;
  width: 100%;
  max-width: 420px;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.6),
    0 0 0 1px rgba(255, 215, 0, 0.15);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
}

.swap-card.pancake-style::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(255, 215, 0, 0.03) 0%, 
    rgba(255, 107, 157, 0.03) 50%, 
    rgba(196, 76, 240, 0.03) 100%);
  border-radius: 32px;
  pointer-events: none;
}

.swap-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  position: relative;
  z-index: 1;
}

.swap-card-header h3 {
