  font-size: 20px;
  font-weight: 600;
  margin: 0;
  text-shadow: 0 0 10px rgba(244, 238, 255, 0.3);
}

.settings-icon {
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0.6;
}

.settings-icon:hover {
  opacity: 1;
  transform: rotate(45deg);
}

/* 代币输入容器 */
.token-input-container {
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 18px 16px;
  margin-bottom: 8px;
  transition: all 0.3s ease;
  position: relative;
  z-index: 1;
}

.token-input-container:hover {
  border-color: rgba(255, 215, 0, 0.3);
  background: rgba(0, 0, 0, 0.4);
}

.token-input-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.token-input-header span {
  color: #B8ADD2;
  font-size: 14px;
  font-weight: 500;
}

.balance {
  color: #7A6EAA;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.max-btn {
  background: linear-gradient(135deg, #1FC7D4, #7645D9);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 2px 6px;
  font-size: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  text-transform: uppercase;
}

.max-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 2px 8px rgba(31, 199, 212, 0.3);
}

.token-input-wrapper {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: space-between;
}

.token-amount-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: #F4EEFF;
  font-size: 24px;
  font-weight: 500;
  padding: 0;
  min-width: 0;
  max-width: calc(100% - 170px);
}

.token-amount-input::placeholder {
  color: rgba(244, 238, 255, 0.3);
}

.token-amount-input:read-only {
  cursor: default;
}

.token-select-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 10px 14px;
  color: #F4EEFF;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 150px;
  max-width: 180px;
  justify-content: space-between;
  white-space: nowrap;
  overflow: hidden;
}

.token-select-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.3);
}

.token-logo {
  font-size: 20px;
}

.token-symbol {
  font-weight: 600;
}

.dropdown-arrow {
  font-size: 12px;
  opacity: 0.6;
  transition: all 0.3s ease;
}

.token-select-btn:hover .dropdown-arrow {
  opacity: 1;
}

/* 交换箭头 */
.swap-arrow-container {
  display: flex;
  justify-content: center;
  margin: -4px 0;
  position: relative;
  z-index: 2;
}

.swap-arrow-btn {
  background: linear-gradient(135deg, #1FC7D4, #7645D9);
  border: 4px solid rgba(39, 38, 44, 0.95);
  border-radius: 50%;
  width: 48px;
  height: 48px;
  color: white;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 25px rgba(31, 199, 212, 0.3);
}

.swap-arrow-btn:hover {
  transform: translateY(-2px) rotate(180deg);
  box-shadow: 0 12px 35px rgba(31, 199, 212, 0.5);
  background: linear-gradient(135deg, #0098A1, #7645D9);
}

/* 交换详情 */
.swap-details {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  padding: 16px;
  margin: 16px 0;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
}

.detail-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 14px;
}

.detail-row:last-child {
  margin-bottom: 0;
}

.detail-row span:first-child {
  color: #B8ADD2;
}

.detail-row span:last-child {
  color: #F4EEFF;
  font-weight: 500;
}

.high-impact {
  color: #ED4B9E !important;
}

.low-impact {
  color: #00D4AA !important;
}

/* 执行按钮 */
.swap-execute-btn {
  background: linear-gradient(135deg, #1FC7D4, #7645D9);
  color: white;
  border: none;
  border-radius: 20px;
  padding: 18px 24px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  margin-top: 16px;
  box-shadow: 0 8px 25px rgba(31, 199, 212, 0.3);
  position: relative;
  z-index: 1;
}

.swap-execute-btn:hover:not(.disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(31, 199, 212, 0.5);
  background: linear-gradient(135deg, #0098A1, #7645D9);
}

.swap-execute-btn.disabled,
.swap-execute-btn:disabled {
  background: linear-gradient(135deg, #474D66, #474D66);
  color: #B8ADD2;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* 模态框覆盖层 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.token-select-modal {
  background: linear-gradient(135deg, 
    rgba(39, 38, 44, 0.98) 0%, 
    rgba(26, 26, 46, 0.95) 50%, 
    rgba(22, 33, 62, 0.98) 100%);
  border-radius: 24px;
  padding: 24px;
  width: 100%;
  max-width: 420px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.8),
