  font-size: 20px;
  font-weight: 600;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  color: #B8ADD2;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #F4EEFF;
}

/* 搜索容器 */
.search-container {
  margin-bottom: 20px;
  display: flex;
  gap: 12px;
}

.token-search-input {
  flex: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 12px 16px;
  color: #F4EEFF;
  font-size: 16px;
  transition: all 0.3s ease;
}

.token-search-input:focus {
  outline: none;
  border-color: rgba(255, 215, 0, 0.5);
  background: rgba(0, 0, 0, 0.5);
}

.token-search-input::placeholder {
