}

.add-token-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 212, 170, 0.4);
}

/* 代币列表 */
.common-tokens h4 {
  color: #B8ADD2;
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 16px 0;
}

.token-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.token-option {
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 12px 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  text-align: left;
}

.token-option:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 215, 0, 0.3);
