}

.token-info {
  flex: 1;
}

.token-info .token-symbol {
  color: #F4EEFF;
  font-size: 16px;
  font-weight: 600;
  margin: 0;
}

.token-info .token-name {
  color: #B8ADD2;
  font-size: 14px;
  margin: 2px 0 0 0;
}

/* 设置模态框 */
.settings-modal {
  background: linear-gradient(135deg, 
    rgba(39, 38, 44, 0.98) 0%, 
    rgba(26, 26, 46, 0.95) 50%, 
    rgba(22, 33, 62, 0.98) 100%);
  border-radius: 24px;
  padding: 24px;
  width: 100%;
  max-width: 480px;
  max-height: 80vh;
  overflow-y: auto;
