.settings-section {
  margin-bottom: 24px;
}

.settings-title {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
  color: #F4EEFF;
  font-size: 16px;
  font-weight: 600;
}

.settings-help {
  color: #B8ADD2;
  font-size: 14px;
