  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 12px;
  color: #F4EEFF;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.gas-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.3);
}

.gas-btn.active {
