  padding: 8px 12px;
  color: #F4EEFF;
  font-size: 14px;
  width: 80px;
  outline: none;
  transition: all 0.3s ease;
}

.deadline-input:focus {
  border-color: rgba(255, 215, 0, 0.5);
  background: rgba(255, 255, 255, 0.15);
}

.deadline-preset {
  background: linear-gradient(135deg, #00D4AA, #1FC7D4);
