.clear-logs-btn {
  padding: 8px 16px;
  background: linear-gradient(135deg, #faad14, #ffc53d);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.clear-logs-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(250, 173, 20, 0.4);
}

/* 部署日志 */
.deployment-logs {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 215, 0, 0.2);
  border-radius: 15px;
  padding: 20px;
  height: 300px;
  overflow-y: auto;
  font-family: 'Courier New', monospace;
}

.deployment-logs::-webkit-scrollbar {
  width: 8px;
}

.deployment-logs::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
}

.deployment-logs::-webkit-scrollbar-thumb {
  background: rgba(255, 215, 0, 0.5);
  border-radius: 4px;
}

.deployment-logs::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 215, 0, 0.7);
}

/* 已部署代币 */
.deployed-tokens {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
}

.token-card {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 15px;
  padding: 25px;
  transition: all 0.3s ease;
}

.token-card:hover {
  border-color: rgba(255, 215, 0, 0.6);
  background: rgba(0, 0, 0, 0.4);
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

.token-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
}

.token-name {
  color: #ffd700;
  font-size: 18px;
  font-weight: 700;
}

.token-symbol {
  background: linear-gradient(135deg, #ff6b9d, #c44cf0);
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
}

.token-details {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;
}

.token-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.token-detail span:first-child {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
}

.token-detail span:last-child {
  color: #ffd700;
  font-weight: 600;
  font-size: 14px;
}

.token-address {
  font-family: 'Courier New', monospace !important;
  font-size: 12px !important;
  word-break: break-all;
}

.token-actions {
  display: flex;
  gap: 10px;
}

.token-action-btn {
  flex: 1;
  padding: 10px 15px;
  background: linear-gradient(135deg, #1FC7D4, #7645D9);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.token-action-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(31, 199, 212, 0.4);
}

/* 响应式设计 */
@media (max-width: 1200px) {
  .form-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .token-type-selector {
    grid-template-columns: 1fr;
  }
  
  .tax-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .protection-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  .launchpad-container {
    padding: 15px;
  }
  
  .launchpad-title {
    font-size: 32px;
  }
  
  .launchpad-section {
    padding: 20px;
  }
  
  .form-grid,
  .tax-grid,
  .protection-grid {
    grid-template-columns: 1fr;
  }
  
  .deploy-controls {
    flex-direction: column;
  }
  
  .deployed-tokens {
    grid-template-columns: 1fr;
  }
  
  .token-actions {
    flex-direction: column;
  }
  
  .deployment-logs {
    height: 250px;
  }
}

/* 地址配置样式 */
.address-help {
  margin-top: 5px;
  font-size: 0.85em;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
}

/* 验证状态徽章 */
.verification-badge {
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.8em;
  font-weight: 600;
  text-transform: uppercase;
}

.verification-badge.verified {
  background: rgba(76, 175, 80, 0.2);
  color: #4CAF50;
  border: 1px solid #4CAF50;
}

.verification-badge.unverified {
  background: rgba(255, 152, 0, 0.2);
  color: #FF9800;
  border: 1px solid #FF9800;
}

/* 验证状态文本 */
.verification-status.verified {
  color: #4CAF50;
}

.verification-status.pending {
  color: #FF9800;
}

/* 已验证按钮样式 */
.token-action-btn.verified {
  background: linear-gradient(135deg, #4CAF50, #45a049);
  border-color: #4CAF50;
}

.token-action-btn.verified:hover {
  background: linear-gradient(135deg, #45a049, #4CAF50);
  box-shadow: 0 0 20px rgba(76, 175, 80, 0.3);
}

/* Gas刷新按钮 */
.refresh-gas-btn {
  background: rgba(255, 215, 0, 0.2);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 8px;
  color: #ffd700;
  padding: 8px 12px;
  margin-left: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
}

.refresh-gas-btn:hover {
  background: rgba(255, 215, 0, 0.3);
  border-color: #ffd700;
  transform: rotate(180deg);
}

/* 代币类型卡片更新 */
.token-type-selector {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

@media (max-width: 768px) {
  .token-type-selector {
    grid-template-columns: 1fr;
  }
  
  .refresh-gas-btn {
    margin-left: 5px;
    padding: 6px 10px;
    font-size: 12px;
  }
} 