.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: 
    linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 25%, #16213e 50%, #0f3460 75%, #533483 100%),
    radial-gradient(circle at 20% 20%, rgba(255, 215, 0, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(255, 107, 157, 0.08) 0%, transparent 50%);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  position: relative;
  overflow: hidden;
}

.App::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 10% 90%, rgba(196, 76, 240, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 90% 10%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  animation: app-background-float 8s ease-in-out infinite;
}

@keyframes app-background-float {
  0%, 100% { 
    transform: translate(0, 0) scale(1);
    opacity: 0.5;
  }
  50% { 
    transform: translate(10px, -10px) scale(1.05);
    opacity: 0.8;
  }
}

.content {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 90px;
  position: relative;
  z-index: 1;
}

/* 华丽的滚动条 */
.content::-webkit-scrollbar {
  width: 8px;
}

.content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
}

.content::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ffd700, #ff6b9d, #c44cf0);
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.content::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #ffed4e, #ff8fab, #d55efc);
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.8);
}

/* 页面过渡动画 */
.page-transition-enter {
  opacity: 0;
  transform: translateY(30px) scale(0.95);
}

.page-transition-enter-active {
  opacity: 1;
  transform: translateY(0) scale(1);
  transition: all 400ms cubic-bezier(0.4, 0, 0.2, 1);
}

.page-transition-exit {
  opacity: 1;
  transform: translateY(0) scale(1);
}

.page-transition-exit-active {
  opacity: 0;
  transform: translateY(-30px) scale(0.95);
  transition: all 400ms cubic-bezier(0.4, 0, 0.2, 1);
}

/* Ant Design 组件深度定制 */
.ant-card {
  box-shadow: 
    0 10px 40px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 215, 0, 0.1) !important;
  background: rgba(12, 12, 12, 0.8) !important;
}

.ant-btn-primary {
  background: linear-gradient(135deg, #ff1744, #ffd700, #9c27b0, #00bcd4) !important;
  border: none !important;
  box-shadow: 0 8px 25px rgba(255, 23, 68, 0.4);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.ant-btn-primary:hover,
.ant-btn-primary:focus {
  background: linear-gradient(135deg, #ff5722, #ffeb3b, #e91e63, #2196f3) !important;
  box-shadow: 
    0 12px 35px rgba(255, 87, 34, 0.6) !important,
    0 0 25px rgba(255, 215, 0, 0.4);
  transform: translateY(-2px);
}

.ant-progress-bg {
  background: linear-gradient(to right, #ff1744, #ffd700, #9c27b0, #00bcd4) !important;
}

.ant-progress-inner {
  background: rgba(0, 0, 0, 0.3) !important;
  box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.5);
}

/* Select下拉框样式 */
.ant-select-dropdown {
  background: rgba(12, 12, 12, 0.95) !important;
  border: 1px solid rgba(255, 215, 0, 0.3) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.8) !important;
  backdrop-filter: blur(20px);
}

.ant-select-item {
  color: rgba(255, 255, 255, 0.8) !important;
}

.ant-select-item:hover {
  background: rgba(255, 215, 0, 0.1) !important;
  color: #ffd700 !important;
}

.ant-select-item-option-selected {
  background: rgba(255, 215, 0, 0.2) !important;
  color: #ffd700 !important;
}

/* Input输入框样式 */
.ant-input {
  background: rgba(0, 0, 0, 0.3) !important;
  border: 2px solid rgba(255, 215, 0, 0.3) !important;
  color: white !important;
}

.ant-input:hover {
  border-color: rgba(255, 215, 0, 0.5) !important;
}

.ant-input:focus {
  border-color: #ffd700 !important;
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2) !important;
}

/* Tag标签样式 */
.ant-tag {
  background: rgba(255, 215, 0, 0.1) !important;
  border: 1px solid rgba(255, 215, 0, 0.3) !important;
  color: #ffd700 !important;
}

/* Divider分割线样式 */
.ant-divider {
  border-color: rgba(255, 215, 0, 0.2) !important;
}

/* 华丽的加载动画 */
@keyframes sparkle {
  0%, 100% { 
    opacity: 0;
    transform: scale(0);
  }
  50% { 
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes rainbow-shift {
  0% { filter: hue-rotate(0deg); }
  100% { filter: hue-rotate(360deg); }
}

/* 华丽的发光效果 */
.glow-effect {
  position: relative;
}

.glow-effect::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, #ffd700, #ff6b9d, #c44cf0, #00d4ff);
  border-radius: inherit;
  z-index: -1;
  animation: rainbow-shift 3s linear infinite;
  opacity: 0.6;
  filter: blur(8px);
}

/* 响应式设计优化 */
@media (max-width: 768px) {
  .App {
    font-size: 14px;
  }
  
  .content {
    padding-bottom: 80px;
  }
  
  .content::-webkit-scrollbar {
    width: 6px;
  }
}

@media (max-width: 480px) {
  .App {
    font-size: 13px;
  }
}

/* 高级动画效果 */
.fade-in-up {
  animation: fadeInUpLuxury 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes fadeInUpLuxury {
  from {
    opacity: 0;
    transform: translateY(40px) scale(0.9);
    filter: blur(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }
}

/* 华丽的焦点效果 */
.luxury-focus:focus-within {
  box-shadow: 
    0 0 20px rgba(255, 215, 0, 0.4),
    0 0 40px rgba(255, 107, 157, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transform: translateY(-1px);
  transition: all 0.3s ease;
}
