.bottom-nav {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 75px;
  background: linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #16213e 100%);
  backdrop-filter: blur(20px);
  border-top: 2px solid;
  border-image: linear-gradient(90deg, #ffd700, #ff6b9d, #c44cf0, #00d4ff) 1;
  justify-content: space-around;
  align-items: center;
  z-index: 1000;
  box-shadow: 
    0 -15px 40px rgba(0, 0, 0, 0.8),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 20px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  color: rgba(255, 255, 255, 0.6);
  min-width: 65px;
  position: relative;
  background: rgba(255, 255, 255, 0.02);
}

.nav-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  background: linear-gradient(135deg, transparent, rgba(255, 215, 0, 0.1));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav-item:hover::before {
  opacity: 1;
}

.nav-item:hover {
  color: #ffd700;
  background: rgba(255, 215, 0, 0.1);
  transform: translateY(-3px);
  box-shadow: 
    0 10px 25px rgba(255, 215, 0, 0.3),
    inset 0 1px 0 rgba(255, 215, 0, 0.2);
}

.nav-item.active {
  color: #ffd700;
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.2), rgba(255, 107, 157, 0.2));
  box-shadow: 
    0 5px 20px rgba(255, 215, 0, 0.4),
    inset 0 1px 0 rgba(255, 215, 0, 0.3);
}

.nav-icon {
  font-size: 22px;
  margin-bottom: 6px;
  transition: all 0.4s ease;
  filter: drop-shadow(0 0 8px currentColor);
}

.nav-item:hover .nav-icon {
  transform: scale(1.1);
  filter: drop-shadow(0 0 15px currentColor);
}

.nav-label {
  font-size: 11px;
  font-weight: 600;
  text-align: center;
  white-space: nowrap;
  text-shadow: 0 0 10px currentColor;
}

.nav-item.ido-center {
  transform: translateY(-25px);
  background: linear-gradient(135deg, #ff6b9d, #ffd700, #c44cf0, #00d4ff);
  color: white;
  border-radius: 50%;
  width: 75px;
  height: 75px;
  box-shadow: 
    0 15px 40px rgba(255, 107, 157, 0.6),
    0 0 0 3px rgba(255, 255, 255, 0.2),
    inset 0 2px 0 rgba(255, 255, 255, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.nav-item.ido-center::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: shimmer 3s infinite;
  border-radius: 50%;
}

@keyframes shimmer {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.nav-item.ido-center:hover {
  transform: translateY(-28px);
  box-shadow: 
    0 20px 50px rgba(255, 107, 157, 0.8),
    0 0 0 4px rgba(255, 255, 255, 0.3),
    inset 0 2px 0 rgba(255, 255, 255, 0.4);
}

.nav-item.ido-center.active {
  background: linear-gradient(135deg, #ff1744, #ffd700, #9c27b0, #00bcd4);
  animation: pulse-rainbow 2s infinite, rotate-glow 4s infinite;
  box-shadow: 
    0 20px 50px rgba(255, 23, 68, 0.8),
    0 0 30px rgba(255, 215, 0, 0.6);
}

.nav-item.ido-center .nav-icon {
  font-size: 26px;
  margin-bottom: 4px;
  z-index: 2;
  position: relative;
}

.nav-item.ido-center .nav-label {
  font-size: 12px;
  font-weight: 700;
  z-index: 2;
  position: relative;
}

@keyframes pulse-rainbow {
  0%, 100% { 
    transform: translateY(-25px) scale(1);
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-25px) scale(1.05);
    filter: hue-rotate(180deg);
  }
}

@keyframes rotate-glow {
  0% { filter: hue-rotate(0deg) brightness(1); }
  50% { filter: hue-rotate(180deg) brightness(1.2); }
  100% { filter: hue-rotate(360deg) brightness(1); }
}

/* 添加粒子效果背景 */
.bottom-nav::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 20% 50%, rgba(255, 215, 0, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 107, 157, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(196, 76, 240, 0.1) 0%, transparent 50%);
  animation: particle-float 6s ease-in-out infinite;
}

@keyframes particle-float {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.7; }
}

/* 响应式设计 */
@media (max-width: 480px) {
  .nav-label { font-size: 10px; }
  .nav-item.ido-center { 
    width: 65px; 
    height: 65px; 
    transform: translateY(-20px);
  }
  .nav-item { 
    min-width: 50px; 
    padding: 8px 10px; 
  }
  .bottom-nav { height: 70px; }
}

/* Add some space for the content to prevent it from being hidden behind the navbar */
body {
  padding-bottom: 70px;
} 